<template>
<transition name="slide-fade">
	<div class="cc-main">
		<img class="cc-logo" src="../assets/img/bg.png" alt="">
		<div class="cc-register">
			<div class="cc-register1">
				<div class="cc-tit">劳务输出人员信息</div>
				<div class="cc-input0"><van-field class="cc-input" label-width="84px" v-model="plnum" type="number" placeholder="需要输出的人员数量" label="劳务人数" /></div>
				<div class="cc-input0"><van-field class="cc-input" label-width="84px" v-model="plprice" placeholder="请输入员工的薪资要求" label="薪资要求" /></div>
				<van-field class="cc-input" label-width="84px" v-model="plrequire" type="textarea" :autosize="{maxHeight:62}" placeholder="其他说明" label="其他要求" />
				<div class="cc-tit1">上传名单（图片、文档）</div>
				<div class="cc-upload1 flexstart">
					<div class="cc-uploaded" v-show="filedata!=''">
						<img @click="del" class="cc-del" src="../assets/img/del.png" alt="">
						<img src="../assets/img/txt.png" alt="">
					</div>
					<div class="cc-uploadicon" v-show="filedata==''">
						<van-icon class="cc-icon" name="plus" />
						<p>添加文件</p>
						<input type="file" id="people-export" ref="inputer" @change="afterRead"/>
					</div>
				</div>
			</div>
			<div class="cc-button" @click="sub">确认登记</div>
		</div>
		<img class="cc-logo1" src="../assets/img/logo1.png" alt="">
	</div>
</transition>
</template>
<script type="text/javascript">
import {Field ,Icon,Dialog,Toast} from 'vant';
export default {
	name: 'home1',
	components: {
		[Field.name]: Field,
		[Icon.name]: Icon,
		[Dialog.name]: Dialog,
		[Toast.name]: Toast,
	},
	data() {
		return {
			plnum:"",
			plprice:"",
			plrequire:"",
			filedata:"",
		};
	},
	mounted() {
	},
	methods: {
		goto(rt){
			this.$router.push({
				path:"/"+rt
			})
		},
		// 上传文件
		afterRead(event) {
			var file = event.target.files;
			var formData = new FormData()
			formData.append('file', file[0])
			Toast.success("上传中...");
			var that=this;
			this.$http.post('/common/uploadFile',formData).then(function (res) {
				console.log(3,res);
				if (res.code==10000) {
					Toast.success(res.msg);
					that.filedata=res.data.url
				}else{
					Toast.fail(res.msg);
				}
			}).catch(function () {
			});
		},
		//删除文件
		del(){
			Dialog.confirm({
				title: '提示',
				message: '您要删除该文件吗？'
			}).then(() => {
				this.filedata=""
			}).catch(() => {
			});
		},
		//提交数据
		sub(){
			var that=this;
			this.$http.post('/main/intermediaryRegister',{
                num:that.plnum,
                salary:that.plprice,
                requirement:that.plrequire,
                fileUrl:that.filedata,
			}).then(function (res) {
				if (res.code==10000) {
					Dialog.alert({
						title: '提示',
						message: '您已登记成功，我们会尽快与您联系，请保持手机畅通！',
						confirmButtonText:'我知道了'
					}).then(() => {
						that.$router.back();
					});						
				}else{
					Toast.fail(res.msg);
				}
			}).catch(function () {
			});
		}
	}
}
</script>
<style lang="scss" scoped>
.cc-logo{
	width:375px;
	height:207px;;
}
.cc-logo1{
	width:375px;
	height:40px;
}
.cc-register{
	padding: 19px 10px;
	background:#1E97A8;
	.cc-register1{
		padding:3px 16px 5px;
		background:#fff;
		border-radius:4px;
		margin-bottom: 20px;
		.cc-input0{
			padding-left: 5px;
			border-bottom: 1px solid #eee;
			position: relative;
			&::after{
				content: '*';
				position: absolute;
				left: 0;
				top: 22px;
				color:red;
			}
		}
		.cc-tit{
			padding:25px 0 14px;
			font-size:18px;
			font-weight:500;
			color:rgba(17,26,52,1);
			line-height:18px;
		}
		.cc-tit1{
			padding:18px 0;
			font-size:16px;
			color:rgba(17,26,52,1);
			line-height:22px;
		}
		.cc-input{
			line-height:32px;
			padding:13px 6px 12px;
			font-size:16px;
			color:rgba(17,26,52,1);
		}
		.cc-upload1{
			padding-bottom: 27px;
			.cc-uploaded{
				width:95px;
				height:95px;
				border-radius:2px;
				border:1px solid #eee;
				position: relative;
				img{
					width:95px;
					height:95px;
				}
				.cc-del{
					position: absolute;
					right: 0;
					top:0;
					width: 20px;
					height: 20px;
				}
			}
			.cc-uploadicon{
				width:95px;
				height:95px;
				border-radius:2px;
				border:1px solid #eee;
				text-align: center;
				padding-top:26px;
				position: relative;
				.cc-icon{
					font-size:28px;
					color:rgba(133,139,156,1);
				}
				p{
					font-size:12px;
					color:rgba(133,139,156,1);
					line-height:16px;
				}
				input{
					position: absolute;
					left: 0;
					top:0;
					width:95px;
					height:95px;
					opacity: 0;
				}
			}
		}

	}
	.cc-button{
		width:343px;
		height:47px;
		line-height:47px;
		text-align: center;
		color:#fff;
		font-size:18px;
		font-weight:500;
		background:#F0C01E;
		border-radius:2px;
		margin: 0 auto 20px;
		&.b2{
			background:#1C5A63;
		}
	}
}
</style>